import { Component, OnInit } from '@angular/core';
import { NavController, ToastController, LoadingController, NavParams, ActionSheetController, AlertController, PopoverController } from '@ionic/angular';
import { WitePrompt, AppMin } from "../core";
import { Storage } from '@ionic/storage';
import { Router, ActivatedRoute } from '@angular/router';
import * as $ from 'jquery'
// var MyOrderListShowPage_message;
// let MyOrderListShowPage_close;

@Component({
  selector: 'app-my-order-list-show',
  templateUrl: './my-order-list-show.page.html',
  styleUrls: ['./my-order-list-show.page.scss'],
})
export class MyOrderListShowPage implements OnInit {
  orderId;
  loader;

  // 输出变量
  name;
  serviceName;
  payDate;
  payMoney;
  userSex;
  userName;
  userAge;
  okDate;
  orderText;
  orderState;

  header_class;
  objUserId;
  objUserImg;

  ClosingDate;// 订单截止日期
  remainingTime;//剩余时间

  ObjBadge = [];//印象标签数组
  objImpressionedSum;//印象标签数
  selectbadgeEd_item = [];//待更新的标签
  addbadgeEd_txt;//要添加的印象标签的txt
  addbadgeEd_txt_sum = 0;//是否已经添加了印象标签 用来判断不能再次添加

  //输入
  saturation_taidu = '0';
  saturation_zhiliang = '0';
  saturation_sudu = '0';

  evaluateText = "";

  constructor(public navCtrl: NavController, public ActivatedRoute: ActivatedRoute, public Router: Router, public storage: Storage, public actionSheetCtrl: ActionSheetController, public popoverCtrl: PopoverController, private toastCtrl: ToastController, public loadingCtrl: LoadingController, public appMin: AppMin, public alertCtrl: AlertController) {





    if (appMin.GetPhoneMode() == "ios") {
      this.header_class = "header_class";
    }
    else {
      this.header_class = "header_class";
      //  console.log("判断为安卓系统 设置状态栏样式");
    }


    this.orderId = this.ActivatedRoute.snapshot.queryParams['orderId'];





  }
  ionViewWillEnter() {
    //页面即将载入


  }
  async  send(value, isshow?: boolean) {
    
    if (isshow) {
      this.loader = await this.loadingCtrl.create({
        message: "加载中...",
        duration: 3000
      });
      await this.loader.present();
    }




    this.appMin.post(JSON.parse(value)).subscribe((data) => {
      this.dataHandle(data);

    })

  }

  addMyOrderListShowPage() {
    //加载初始数据
    let obj = {
      type: 'addMyOrderListShowPage',
      orderId: this.orderId,
      userId: this.appMin.getUserid()
    }
    this.send(JSON.stringify(obj));
  }
  dataHandle(meg: any) {

    if (meg == null) {
      return;
    }

    if (meg['type'] == "addMyOrderListShowPage_ret") {
      //  WitePrompt('addMyOrderListShowPage_ret!');

      this.name = meg['name'];
      this.serviceName = meg['serviceName'];
      this.payDate = meg['payDate'];
      this.payMoney = meg['payMoney'];
      this.userSex = meg['userSex'];
      this.userName = meg['userName'];
      this.userAge = meg['userAge'];
      this.okDate = meg['okDate'];
      this.orderText = meg['orderText'];
      this.objUserId = meg['objUserId'];
      this.objUserImg = meg['objUserImg'];
      this.ClosingDate = meg['截止日期']//截止日期
      this.remainingTime = meg["剩余时间"]//剩余时间
      if (this.orderText == '') {
        this.orderText = "没填写";
      }
      this.orderState = meg['orderState'];

    }
    if (meg['type'] == "submitEvaluate_ret") {
      //提交订单返回值
      if (meg['isTransactionOk'] == true) {


        this.presentToast("评价成功！")

        //2 重新请求数据
        this.addMyOrderListShowPage();


      }
    }

    if (meg['type'] == 'getObjBadge_ret') {
      //取得咨询师印象标签
      // console.log('getObjBadge_ret=' + JSON.stringify(meg))
      if (meg['bq'] != undefined && meg['bq'] != null) {
        this.ObjBadge = meg['bq'];
        this.objImpressionedSum = this.ObjBadge.length;
      }

    }



  }
  saturation_taidu_click(obj, val) {

    //设置数据
    if (obj == "saturation_taidu") {
      this.saturation_taidu = val;
    }
    if (obj == "saturation_zhiliang") {
      this.saturation_zhiliang = val;
    }
    if (obj == "saturation_sudu") {
      this.saturation_sudu = val;
    }

    $('#' + obj + ' img').attr('src', '../../assets/img/未评价.png');

    let i = 0;
    $('#' + obj + ' img').each(function (obj) {
      $(this).attr('src', '../../assets/img/评价.png');
      i++;
      if (i == val) {
        return false;
      }
    });


  }
  async presentAlert(val) {
    let alert = await this.alertCtrl.create({
      header: '提示',
      subHeader: val,
      buttons: ['确定']
    });
    alert.present();
  }

  getObjBadge() {
    //加载咨询师标签
    let obj = {
      type: 'getObjBadge',
      orderId: this.orderId
    }

    this.send(JSON.stringify(obj));
  }

  async addbadgeEd() {
    //添加新标签

    if (this.addbadgeEd_txt_sum == 1) {
      //超过了最大数量 弹出提示

      this.presentAlert("最多添加1个");
      return;
    }



    let alert = await this.alertCtrl.create({
      header: '添加新标签',
      inputs: [
        {
          name: 'name',
          placeholder: '标签名'
        }
      ],
      buttons: [
        {
          text: '取消',
          role: 'cancel',
          handler: data => {
            //   console.log('Cancel clicked');
          }
        },
        {
          text: '确定',
          handler: data => {
            // let txt = data.name;

            let obj = {
              txt: data.name,
              sum: '1',
              id: ''

            }
            this.ObjBadge.push(obj);

            this.addbadgeEd_txt = data.name;
            this.addbadgeEd_txt_sum = 1;

          }
        }
      ]
    });
    alert.present();
  }
  selectbadgeEd(item) {
    //更新标签
    //  console.log("selectbadgeEd()=" + JSON.stringify(item));
    let id = item.id;
    //  console.log("selectbadgeEd id=" + id);

    if ($.inArray(id, this.selectbadgeEd_item) >= 0) {
      //数组中存在该项
      //   console.log("数组中存在该项=" + id);
    }
    else {
      this.selectbadgeEd_item.push(id);//添加到数组
      //  console.log("添加进数组=" + id);



      //遍历并临时显示计数器+1
      for (let i = 0; i < this.ObjBadge.length; i++) {
        if (this.ObjBadge[i].id == id) {
          this.ObjBadge[i].sum = parseInt(this.ObjBadge[i].sum) + 1;

          //   console.log("找到对应的标签ID+1");

        }
      }

    }
    //   console.log("selectbadgeEd 当前数组序列=" + this.selectbadgeEd_item);

  }
  async submitEvaluate() {
    //提交评价
    if (this.judgeCanSubmitEvaluate()) {

      //弹出提示框
      let alert = await this.alertCtrl.create({
        header: '提交评价',
        message: '请确认你已经咨询完毕?',
        buttons: [
          {
            text: '等下',
            role: 'cancel',
            handler: () => {
              //   console.log('等下 clicked');
            }
          },
          {
            text: '提交',
            handler: () => {
              //  console.log('提交 clicked');
              let obj = {
                type: 'submitEvaluate',
                userId: this.appMin.getUserid(),
                orderId: this.orderId,
                saturation_taidu: this.saturation_taidu,
                saturation_zhiliang: this.saturation_zhiliang,
                saturation_sudu: this.saturation_sudu,
                evaluateText: this.evaluateText,
                selectbadgeEd_item: this.selectbadgeEd_item.join(','), //要更新的印象标签的id 数组
                addbadgeEd_txt: this.addbadgeEd_txt,//新添加的印象标签的txt
              }

              this.send(JSON.stringify(obj), true);
              //   console.log("submitEvaluate=" + JSON.stringify(obj));
            }
          }
        ]
      });
      alert.present();

    } else {
      //不能提交
      this.presentToast('请先打分在提交');
      return;
    }




  }
  judgeCanSubmitEvaluate(): boolean {
    if (this.saturation_taidu != '0' && this.saturation_zhiliang != '0' && this.saturation_sudu != '0') {
      return true;

    } else {
      return false;
    }
  }
  async presentToast(text) {
    let toast = await this.toastCtrl.create({
      message: text,
      duration: 1500,
      position: 'top'
    });



    toast.present();
  }
  async  presentPopover() {




    let actionSheet = await this.actionSheetCtrl.create({
      // header: '',
      buttons: [
        {
          text: '投诉咨询师',
          // role: 'destructive',

          cssClass: 'iconfont',

          handler: () => {
            let obj = {
              orderId: this.orderId,
              toUserCName: this.name
            }
            this.Router.navigate(["complaint"], { queryParams: obj })

          }
        }, {
          text: '取消',
          role: 'cancel',
          handler: () => {
            WitePrompt('取消_click');
          }
        }
      ]
    });
    await actionSheet.present();

  }



  ngOnInit() {
    this.addMyOrderListShowPage();
    this.getObjBadge();
  }

}
